<template>
	<view class="container">
		<view class="title">提现</view> 
		<view class="input">
			<view>￥</view>
			<uni-easyinput v-model="money" :inputBorder="false" style="flex: 1;"></uni-easyinput>
			<text style="font-size: 24rpx;color: #B197A6;" @click="all">全部</text>
		</view>
		<view class="tips">
			可提现余额{{userinfo.account.user_money}}元
		</view>
		<view class="add" @click="tixian">
			确认提现
		</view>
	</view>
</template>

<script>
 
	export default {
		data() {
			return {
				money:'',
				userinfo: {
					account:{
						user_money:0
					}					
				},
			}
		},
		onLoad() {
			
		},
		onShow() {
			this.getUserInfo()
		},
		components: {
			 
		},
		methods: {
			all(){
				this.money = this.userinfo.account.user_money
			},
			async getUserInfo() {
				let res = await this.$u.get('/v2/member/member/view', {
					id: this.vuex_user.id
				})
				this.userinfo = res.data
			},
			async tixian(){
				if(this.$u.test.isEmpty(this.money)){
					this.$u.toast('金额不能为空')
					return
				}
				if(!this.$u.test.number(this.money)){
					this.$u.toast('金额格式不对')
					return
				}
				if(this.money > this.userinfo.account.user_money){
					this.$u.toast('金额超过最大数')
					return
				}
				if(this.money<=0){
					this.$u.toast('金额不对')
					return
				}
				let res = await this.$u.get('/v2/shop/record/save', {
					uid: this.vuex_user.id,
					money:this.money
				})
				if(res.code==200){
					this.$u.toast('提现成功，请等待审核')
					this.getUserInfo()
				}else{
					this.$u.toast(res.message)
				}
			}
		}
	}
</script>
<style>
	page {}
</style>
<style scoped>
	.title{
		font-size: 36rpx;
		font-weight: 800;
		color: #323233;
		margin: 30rpx;
	}
	.tips{
		font-size: 28rpx;
		color: #969799;
		margin: 30rpx;
	}
	.input{
		display: flex;
		margin: 30rpx;
		border-bottom: #eee 1px solid;
		align-items: center;
		 
	}
	.input .fuhao{
		font-size: 28rpx;
		font-weight: bold;
	}
	.add {
		margin: 40rpx 20rpx;
		background: linear-gradient(83deg, #FF4400 0%, #FF6600 100%);
		height: 88rpx;
		border-radius: 44rpx;
		display: flex;
		justify-content: center;
		align-items: center;
		color: #FFF;
		width: 690rpx;
	}
</style>
